.table {
    margin-top: 20px;
    overflow: scroll;
    height: 500px;
    color: #6a5d5d;
}

.table tr:nth-of-type(odd) {
    background-color: #f3f2f8;
}

.table tr {
    display: flex;
    justify-content: space-between;
}

.table td {
    padding: .5rem;
}

img {
    height: 25px;
    width: 50px;
}